import React, { Component } from 'react';
import axios from 'axios';

class Form extends Component {

  constructor() {
    super()
    this.state = {
    name: '',
    email: '',
    budget: '',
    message: ''
  }
  this.handleChange = this.handleChange.bind(this)
  this.handleSubmit = this.handleSubmit.bind(this)
}

handleChange = e => {
  this.setState({[e.target.name]: e.target.value })
}
async handleSubmit(e){
  e.preventDefault()
   e.target.reset(alert('Your message has been sent.'));



  const { name, email, budget, message } = this.state

  /* eslint-disable no-unused-vars */
  const form = await axios.post('/api/form', {
    name,
    email,
    budget,
    message
  })
}

  render() {
    require('./form.css');
    return (

      <form onSubmit={this.handleSubmit.bind(this)}>
      <input type="text" name="name" onChange={this.handleChange} placeholder="Name" required="required" />
      <input type="email" name="email" onChange={this.handleChange} placeholder="Email" required="required"/>
      <input type="text" name="budget" onChange={this.handleChange} placeholder="Budget (optional)"/>
      <textarea type="textarea" name="message" onChange={this.handleChange} placeholder="Describe your project..." required="required"></textarea>
      <input id="submit" type="submit" value="Send Inquiry" />
      </form>

    );
  }
}
export default Form;



